@import "./src/assets/themes/appdb/variables.scss";

@import "@/assets/variables";

.main {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;

  @include notebook {
    flex-direction: column;
    align-items: center;
  }

  @media (max-width: 767px) {
    align-items: stretch;
  }
}

.heading {
  display: block;
  width: 100%;
  color: var(--text-tertiary);
  font-size: 20px;
  font-weight: 600;
  margin-bottom: 20px;

  @include notebook {
    text-align: center;
  }

  @media (max-width: 767px) {
    text-align: left;
  }
}

.filters {
  width: 180px;

  @include notebook {
    width: auto;
    margin-bottom: 10px;
  }

  @media (max-width: 767px) {
    width: 100%;
  }
}

.apps {
  margin: 0 20px;
  flex-grow: 1;

  @include notebook {
    margin: 30px 0 0;
    order: 1;
  }
}

.notifications {
  flex: 0 0 300px;

  @include notebook {
    flex: 0 0 auto;
    order: 0;
  }
}

.notification {
  background-image: var(--pink-gradient);
  border: 1px solid var(--pink-border);
  border-radius: 10px;
  padding: 15px;
  margin: 0;
  color: var(--text-primary);

  &__heading {
    font-size: 20px;
    color: var(--text-tertiary);
    font-weight: 600;
    margin-bottom: 10px;
  }

  &__text {
    line-height: 20px;
  }

  &__link {
    display: inline-block;
    background: var(--bg);
    border: 1px solid $primary;
    border-radius: 2px;
    color: $primary;
    padding: 7px 10px;
    margin-top: 10px;
  }
}

.request-modal {
  .request-modal__checkbox {
    margin-top: 10px;

    & + * {
      margin-top: 10px;
    }
  }
}
